@import "/src/Resources/scss/customs-mixin.scss";
@import "/src/Resources/scss/variables/variables";

#UserRecords {
    box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.06);
    .nav-styles {
        box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.075);
    }
    
    .tab-styles {
        background-color: #F8F8F8;
    }

    .wrapper {
        padding: 15px;

        .label {
            font-size: 15px;
            line-height: 21px;
            color: #2C2C26;
        }

        .text {
            font-size: 15px;
            line-height: 21px;
            color: #5F5974;
        }

        .title {
            font-weight: 500;
            font-size: 20px;
            line-height: 24px;
            color: $primary;
        }


        .wrapper-info {
            @include displayRow(35px, flex-start, center);

            min-width: fit-content;
            margin-top: 10px;
        }
    }
}