@import "/src/Resources/scss/customs-mixin.scss";
@import "/src/Resources/scss/variables/variables";
#UserDetails {
    box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.06);
    .wrapper-icons {
        height: 100%;
        display: flex;
        flex-direction: column;
        // align-items: start;
        // justify-content: start;
        gap: 5px;
    }
    .wrapper {
        display: grid;
        grid-template-columns: 1fr 2fr;

        .label {
            font-size: 15px;
            line-height: 21px;
            color: #2C2C26;
        }

        .text {
            font-size: 15px;
            line-height: 21px;
            color: #5F5974;
        }

        .user-left-info {
            @include displayColumn(10px);

            margin-right: 15px;
            border-right:  0.5px solid #D8D8D8;

            .name {
                font-size: 25px;
                line-height: 30px;
                color: #2C2C26;
            }

            .user-name {
                font-size: 20px;
                font-weight: 300;
                line-height: 21px;
                color: #5F5974;
            }

            .badge-wrapper {
                @include displayRow(5px, space-between, center);
                padding-right: 3px;

                .incomplete-wrapper {
                    align-self: flex-start;
                }
            }

            .badge-style {
                font-size: 12px;
                line-height: 14px;
                text-align: center;
                width: fit-content;
                height: fit-content;
        
                &.admin {
                    color: #528DFF !important;
                    background-color: rgba(82, 141, 255, 0.12) !important;
                }

            }
        }

        .wrpper-email-phone {
            @include displayRow(25px, start, center);

            .icon-color {
                color: #1B9AAA;
            }
        }

        .educatoin-section {
            margin-top: 15px;
            @include displayColumn(10px);

            .education-title {
                font-weight: 500;
                font-size: 20px;
                line-height: 24px;
                color: $primary;
            }

            .eduction-info {
                display: grid;
                row-gap: 15px;
                grid-template-columns: repeat(2, 1fr);
            }

        }
        
        .content-group {
            @include displayRow(5px, space-between, center);
        }
        .group-icon-text {
            @include displayRow(5px, baseline, center);
        }
    }
}