#UserFiles {
    .grid-wrapper {
        gap: 1.25rem;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(22.5rem, 1fr));

        .file-title {
            font-size: 13px;
            line-height: 15px;
            color: #2C2C26;
        }
        .card {

            &:hover {
                background-color: darken($color: #fff, $amount: 5);
            }
            
            .card-body {
                padding: 10px;
                display: flex;
                padding-left: 15px;
                flex-direction: row;
                align-items: center;
                justify-content: flex-start;

                p {
                    width: 90%;
                    padding: 2px 5px;
                    overflow: hidden;
                    white-space: nowrap;
                }

                .text-name {
                    font-size: 12px;
                    line-height: 14px;
                    color: #2C2C26;
                    text-overflow: ellipsis;
                }

            }
        }
    }

}